const config = {
    duration: 1 //seconds
}

let previouseVal;
let operatoricon;

document.querySelectorAll(".one-up select").forEach(item => {
    item.addEventListener("change", e => {

        let popUpStyle = document.createElement("style");
        let id = "copy-" + Math.ceil(Math.random() * 100000);
        e.currentTarget.parentNode.classList = id + " one-up";

        if (e.currentTarget.value > previouseVal) {
            operatoricon = " +";
        } else {
            operatoricon = "-";
        }

        popUpStyle.appendChild(document.createTextNode(`
        ${'.' + id + '.one-up::before'} {
            font-weight: bold;
            content: "${e.currentTarget.value}";
            position: absolute;
            margin-left: 2em;
        }
        ${'.' + id + '.one-up::after'} {
            content: "${operatoricon}";
            position: absolute;
            margin-left: 2em;
        }
        `));
        e.currentTarget.parentNode.appendChild(popUpStyle);

        setTimeout(() => {
            popUpStyle.appendChild(document.createTextNode(`
            ${'.' + id + '.one-up::before'} {
                margin-top: -100px;
                margin-left: 2em;
                transform: scale(5);
                opacity: 0;
                transition-duration: ${config.duration}s;
                transition-timing-function: ease-out;
            }
            ${'.' + id + '.one-up::after'} {
                margin-top: -50px;
                margin-left: 1em;
                transform: scale(5);
                opacity: 0;
                transition-duration: ${config.duration * 1.5}s;
                transition-timing-function: ease;
            }
            `));
        });

        setTimeout(() => {
            function removeFromDOM(className) {
                document.body.querySelectorAll("." + className).forEach(n => n.remove());
            }
            removeFromDOM("one-up style");
        }, config.duration * 1000);
    });

    item.addEventListener("click", e => {
        previouseVal = e.currentTarget.value;
    });
})